.header {
  margin: 0 auto;
  width: 37.5rem;
  height: 5.1rem;
  background-color: #2b2b2b;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  font-size: 2rem;
  letter-spacing: .4rem; }
  @media (min-width: 1280px) {
    .header {
      width: 144rem;
      height: 8rem; } }
  .header__input-container {
    display: none; }
    @media (min-width: 1280px) {
      .header__input-container {
        width: 35rem;
        background-color: #fff;
        display: flex;
        flex-direction: row;
        align-items: center;
        border: 0.2rem solid #2b2b2b;
        border-radius: 1rem; } }
  .header__input {
    display: none; }
    @media (min-width: 1280px) {
